.badge {
  display: inline-flex;

  gap: var(--#{$cdk}size-4);
  align-items: center;

  &:has(.material-icons) {
    gap: var(--#{$cdk}size-4);
  }

  &.status {
    padding: 0 var(--#{$cdk}size-5);
    font-size: var(--#{$cdk}size-14);
    font-weight: 500;
    line-height: 1.5;
  }

  .material-icons {
    font-size: 1.5em;
  }
}

